import tabBar from '../../common/tabbar.vue';
import http from '../../common/http.js';
import util  from '../../common/util.js';
let navId = 0;
let nowpage = 1;
let totalPage = 0;
let isSearch = false;
let interval = null;
// 进入界面的
let isFixedHeight = false;
// 搜索的时候的
let isFixedHeight2 = false;
export default {
	data() {
		return {
			totalRows: '0',
			isShowNoResult: false,
			contentHeight: 0,
			platForm: 0,
			searchKeyword: "",
			navigation: {
				height: 88,
				top: 30,
				left:17,
				title: ""
			},
			iconSize: 20,
			searchText: '',
			articleList: [ 
				
			]
		}
	},
	components: {
		tabBar:tabBar,
	},
	computed: {
		level: function() {
			//console.log('userInfo:', this.$store.getters.userInfo);
			let userInfo = util.getUserInfoFromStorage();
			if (userInfo.level) {
				return userInfo.level;
			}
			if(this.$store.getters.userInfo.level) {
				return this.$store.getters.userInfo.level;
			} else {
				return 0;
			}
		},
		uid: function() {
			let userInfo = util.getUserInfoFromStorage();
			if (userInfo.uid) {
				return userInfo.uid;
			}
			if(this.$store.getters.userInfo.uid) {
				return this.$store.getters.userInfo.uid;
			}
			return 0;
		},
		scrollHeight: function() {
			let scrollHeight = 0;
			let sysinfo = uni.getSystemInfoSync();
			let windowHeight = sysinfo.windowHeight;
			if(getApp().globalData.isIphoneX) {
				windowHeight = windowHeight - 34;
			}
			// 长度为0,则高度变化为:
			if(this.articleList.length == 0 && this.isShowNoResult) {
				scrollHeight = windowHeight - 350;
			} else {
				//this.navigation.height = statusBarHeight;
				scrollHeight = windowHeight - 50;
			}
			return scrollHeight;
		}
	},
	onShareAppMessage() {
		//console.log('share....');
		let pages = getCurrentPages() //获取加载的页面
		let currentPage = pages[pages.length-1] //获取当前页面的对象
		let url = currentPage.route //当前页面url
		let options = currentPage.options;
		//console.log('options', options);
		let query = util.buidQuery(options);
		let title = options.title ? decodeURI(options.title): '"搜索结果"';
		return {
			title: title,
			path: url+"?"+query,
			success: function() {
				console.log('share success!');
			},
			fail: function() {
				console.log('share..sucess!');
			}
		};
	},
	watch:{
		
	},
	onPullDownRefresh() {
		//console.log('页面下拉刷新');
		
		let _self = this;
		isFixedHeight = false;
		_self.getHuashuArticleList();
		setTimeout(function() {				
			uni.stopPullDownRefresh();
		}, 100);
	},
	onLoad(option) {
		//console.log('option', option);
		this.setIosBackground();
		let sysinfo = uni.getSystemInfoSync();
		this.contentHeight = sysinfo.windowHeight;
		let title = option.title;
		// 获取keyword,
		let key  = option.keyword;
		navId = option.navId;
		if (key) {
			this.searchKeyword = key;
			// 判断从搜索框进入的。
			isSearch = true;
			uni.setNavigationBarTitle({
				title:'"搜索结果"'
			});
		}
		if(title) {
			title = decodeURIComponent(title);
			uni.setNavigationBarTitle({
				title:title
			});
		}
		this.platForm = getApp().globalData.platform;
		uni.startPullDownRefresh();
	},
	methods: {
		suggest() {
			uni.navigateTo({
				url:'/pages/user/complaint'
			});
		},
		setIosBackground() {
			if (getApp().globalData.platform == 2) {
				console.log('setIosBackgroundColor:');
				uni.setBackgroundColor({
					backgroundColorTop: "#2369E6", // 顶部窗口的背景色为蓝色
					backgroundColorBottom: "#15B9DA", // 底部窗口的背景色为绿
				});
			};
		},
		getHuashuArticleList() {
			let keyword = this.searchKeyword;
			//console.log('navId', navId);
			let classid = navId;
			let params = {};
			if (keyword) {
				params = { keyword: keyword };
			} else if (classid) {
				params.cid = classid;
			}
			params.uid = this.uid;
			params.nowpage = 1;
			// 搜索框搜索时候,将nowpage 置位1
			nowpage = 1;
			const data = getApp().globalData;
			const apiPrefix = data.serverUri;
			const auth = data.auth;
			params.auth = auth;
			let url = apiPrefix + "?mod=loveword&ac=list";
			if(keyword) {
				url = apiPrefix + "?mod=loveword&ac=search";
			}
			params.filterData = true;
			// 通过栏目获取话术列表接口
			http.request(url, params).then(resp=>{
				//console.log('resp', resp);
				if(resp.status == 1) {
					let articleData  = resp.data;
					this.articleList =  articleData;
					totalPage   = resp.totalpage;
					let t = parseInt(resp.total);
					this.totalRows = t;
					// 第一页显示完整
					// 第一页的多加几个...
					this.isShowNoResult = false;
				} else {
					this.articleList =  [];
					totalPage   = 0;
					this.totalRows = 0;
					if (keyword) {
						this.isShowNoResult = true;
						if (getApp().globalData.platform == 2) {
							console.log('setIosBackgroundColor:');
							uni.setBackgroundColor({
								backgroundColorBottom: "#17ACDC", // 底部窗口的背景色为绿
							});
						}
					}
				}
			});
		},
		searchKeywordFunc() {
			// 调用搜索关键词接口...
			let keyword = this.searchKeyword;
			if(!keyword || keyword.length<2) {
				uni.showToast({
					title: '请输入关键词，且至少2个字符长度!',
					icon:"none",
					duration: 2000
				});
				return;
			}
			uni.setNavigationBarTitle({
				title:'"搜索结果"'
			});
			isFixedHeight = false;
			let params = {
				filterData:true
			};
			if (keyword) {
				params.keyword = keyword;
			}
			// 表示搜索框的结果...
			isSearch = true;
			params.uid = this.uid;
			params.nowpage = 1;
			this.params = params;
			// 通过关键词获取话术列表接口
			const data = getApp().globalData;
			const apiPrefix = data.serverUri;
			const auth = data.auth;
			params.auth = auth;
			const url = apiPrefix + "?mod=loveword&ac=search";
			http.request(url, params).then(resp=>{
				//console.log('resp', resp);
				if(resp.status == 1) {
					this.articleList = resp.data;
					totalPage   = resp.totalpage;
					let t = parseInt(resp.total);
					this.totalRows = t;
					// 第一页显示完整
					this.isShowNoResult = false;
				} else {
					this.articleList = [];
					this.totalRows = 0;
					this.isShowNoResult = true;
					if (getApp().globalData.platform == 2) {
						console.log('setIosBackgroundColor:');
						uni.setBackgroundColor({
							backgroundColorBottom: "#17ACDC", // 底部窗口的背景色为绿
						});
					}
					return;
				}
			});
		},
		back() {
			uni.navigateBack({
				url:'/pages/index/index',
			});
		},
		upgrade_vip() {
			// 这里跳转到登录界面
			if(this.level<2) {
				console.log('go here!');
				uni.switchTab({
					url: '/pages/user/index'
				});
				return;
			}
			uni.navigateTo({
				url:'/pages/user/upgrade_user_vip'
			});
		},
		copyHuashu(line) {
			let content = line.split('@@')[1];
			//console.log('content', content);
			uni.setClipboardData({
				data: content,
				success: function(res) {
					uni.getClipboardData({
						success: function(res) {
							uni.showToast({
								title: '复制成功'
							});
						}
					});
				}
			});
		}
	},
	onReachBottom() {
		let _self = this;
		if(isSearch && (!this.searchKeyword || this.searchKeyword.length<2)) {
			// 显示空数据给用户
			return;
		}
		//console.log('here!', this.level);
		if (nowpage>=totalPage && this.level>1) {
			console.log('here!');
			if (interval) {
				clearTimeout(interval);
			}
			if(nowpage == totalPage) {
				// 更改这里的
			}
			return;
		}
		
		if (this.level<=1) {
			let m = parseInt(this.totalRows);
			if(m<=10) {
				return;
			}
			// 最后一页应该加的元素个数
			if (interval) {
				clearTimeout(interval);
			}
			return;
		}
		uni.showLoading({
			title:"玩命加载中..."
		});
		interval = setTimeout(function() {
			nowpage++;
			if(_self.level<=1) {
				uni.hideLoading();
				return;
			}
			let params = {};
			params.nowpage = nowpage;
			params.cid = navId;
			params.uid = _self.uid;
			const data = getApp().globalData;
			const apiPrefix = data.serverUri;
			const auth = data.auth;
			params.auth = auth;
			let url = '';
			if(isSearch) {
				url = apiPrefix + "?mod=loveword&ac=search";
				params.keyword = _self.searchKeyword;
			} else {
				url = apiPrefix + "?mod=loveword&ac=list";
			}
			params.filterData = true;
			http.request(url, params).then(resp=>{
				//console.log('resp', resp);
				if(resp.status == 1) {
					uni.hideLoading();
					let len = resp.data.length;
					for(let j = 0;j<len;j++) {
						_self.articleList.push(resp.data[j]);
					}
					//console.log(_self.articleList);
					totalPage   = resp.totalpage;
					this.totalRows = resp.total;
				} else {
					totalPage   = 0;
					this.totalRows = 0;
				}
			});
			uni.hideNavigationBarLoading();
		}, 400);
	}
}
